"use client";

import { useEffect, useRef, useState } from "react";
import Content from "@/components/ui/Content";
import Hero from "@/components/ui/Hero";
import Footer from "@/components/ui/Footer";
import TextScroll from "@/components/ui/TextScroll";
import SocialLinks from "@/components/ui/SocialLinks";

function useTiltToMouse() {
  useEffect(() => {
    const triggers = document.querySelectorAll('[data-tilt-to-mouse="1"]');
    const listeners: { trigger: Element; fn: (e: DeviceOrientationEvent) => void }[] = [];

    triggers.forEach((trigger) => {
      const maxX = trigger.clientWidth;
      const maxY = trigger.clientHeight;
      const centerX = maxX / 2;
      const centerY = maxY / 2;

      let clientX = centerX;
      let clientY = centerY;

      const X_SENSITIVITY = parseFloat(trigger.getAttribute("data-tilt-to-mouse-x-sensitivity") || "1");
      const Y_SENSITIVITY = parseFloat(trigger.getAttribute("data-tilt-to-mouse-y-sensitivity") || "1.6");
      const X_OFFSET = parseFloat(trigger.getAttribute("data-tilt-to-mouse-x-offset") || "0");
      const Y_OFFSET = parseFloat(trigger.getAttribute("data-tilt-to-mouse-y-offset") || "0");

      const handleOrientation = (event: DeviceOrientationEvent) => {
        if (event.beta == null || event.gamma == null) return;

        const y = event.beta;
        const x = event.gamma;

        clientX = centerX + maxX * (x / 90 + X_OFFSET) * X_SENSITIVITY;
        clientY = centerY + maxY * (y / 180 + Y_OFFSET) * Y_SENSITIVITY;

        trigger.dispatchEvent(
          new MouseEvent("mousemove", {
            bubbles: true,
            cancelable: false,
            clientX,
            clientY,
            view: window,
          })
        );
      };

      window.addEventListener("deviceorientation", handleOrientation);
      listeners.push({ trigger, fn: handleOrientation });
    });

    return () => {
      listeners.forEach(({ fn }) => {
        window.removeEventListener("deviceorientation", fn);
      });
    };
  }, []);
}

function Page() {
  useTiltToMouse();

  const sectionRef = useRef<HTMLDivElement | null>(null);
  const [shrinkSidebar, setShrinkSidebar] = useState(false);
  const [overlayHeight, setOverlayHeight] = useState("50vh");
  const [navOpen, setNavOpen] = useState(false);

  // Scroll effect
  useEffect(() => {
    const handleScroll = () => {
      if (!sectionRef.current) return;

      const section = sectionRef.current;
      const rect = section.getBoundingClientRect();
      const windowHeight = window.innerHeight;
      const scrollProgress = Math.min(Math.max((windowHeight - rect.top) / rect.height, 0), 1);

      // Update overlay height (shrink gradually)
      const newHeight = `${50 - scrollProgress * 30}vh`; // 50vh ‚Üí 20vh
      setOverlayHeight(newHeight);

      // Shrink sidebar at 80% scroll
      setShrinkSidebar(scrollProgress > 0.8);
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <section ref={sectionRef} className="">
      {/* Navigation */}
      <div
        className={`navigation-wrap w-hidden-tiny h-screen transition-all duration-500`}
        style={{ width: shrinkSidebar ? "80px" : "185px" }}
      >
        <img src="/2-removebg-preview.png" alt="" className="logo-img w-hidden-tiny w-full" />

        {/* Scroll Down Arrow */}
        <a
          href="#"
          className="arrows-wrap w-hidden-tiny w-inline-block"
          style={{ willChange: "opacity", opacity: 1 }}
        >
          <div className="arrow-half-wrap">
            <div className="arros-fontawesome">ÔÑÉ</div>
            <div
              className="arrow-color-overlay white"
              style={{ transform: "translate3d(0,0,0)" }}
            ></div>
          </div>
          <div className="arrow-half-wrap">
            <div className="arros-fontawesome light">ÔÑÉ</div>
            <div className="arrow-color-overlay" style={{ transform: "translate3d(0,0,0)" }}></div>
          </div>
        </a>

        {/* Sidebar */}
        <div className="side-navbar w-nav">
          <div className="side-nav-container w-container">
            {/* Nav Button */}
            <div
              className="menu-button-2 w-nav-button"
              aria-label="menu"
              role="button"
              tabIndex={0}
              onClick={() => setNavOpen((prev) => !prev)}
            >
              <div className="icon w-hidden-tiny w-icon-nav-menu"></div>
            </div>

            {/* Overlay shrink */}
            <div className="block-overlay w-hidden-tiny transition-all duration-500" style={{ height: overlayHeight }}></div>

            {/* Nav Links */}
            <nav
              role="navigation"
              className={`side-nav-menu w-nav-menu transition-all duration-500 ${navOpen ? "block" : "hidden"}`}
            >
              {shrinkSidebar ? (
                <>
                  <a href="/" className="side-nav-link w-nav-link" title="Home">üè†</a>
                  <a href="/projects" className="side-nav-link w-nav-link" title="My Works">üíº</a>
                  <a href="#about" className="side-nav-link w-nav-link" title="About">‚ÑπÔ∏è</a>
                  <a href="#contact" className="side-nav-link w-nav-link" title="Contact">üìû</a>
                </>
              ) : (
                <>
                  <a href="/" className="side-nav-link w-nav-link">Home</a>
                  <a href="/projects" className="side-nav-link w-nav-link">My works</a>
                  <a href="#about" className="side-nav-link w-nav-link">About</a>
                  <a href="#contact" className="side-nav-link w-nav-link">Contact</a>
                </>
              )}
            </nav>
          </div>
        </div>

        {/* Social Links */}
        <SocialLinks />
      </div>

      {/* Hero Section */}
      <Hero />

      <TextScroll />

      {/* Content */}
      <Content />
    </section>
  );
}

export default Page;
